// Servicio para manejar las peticiones AJAX a la API

// URL base de la API
const API_URL = 'https://qans224.diegoncurso.es/api';

// Función genérica para realizar peticiones fetch
const fetchData = async (endpoint, options = {}) => {
  try {
    // Importar utilidades de red
    const { handleHttpResponse, retryFetch, getNetworkErrorMessage } = await import('../utils/networkUtils');
    
    // Usar la función de reintento para manejar fallos temporales de red
    const fetchWithRetry = () => fetch(`${API_URL}/${endpoint}`, {
      headers: {
        'Content-Type': 'application/json',
        ...options.headers
      },
      ...options
    }).then(handleHttpResponse);
    
    // Intentar la petición con reintentos
    const response = await retryFetch(fetchWithRetry, 2, 1000);
    
    // Intentar parsear la respuesta como JSON
    const data = await response.json();
    return data;
  } catch (error) {
    // Mejorar el mensaje de error con información adicional
    const enhancedError = new Error(`Error en la petición API a ${endpoint}: ${error.message}`);
    enhancedError.originalError = error;
    enhancedError.endpoint = endpoint;
    enhancedError.options = options;
    
    // Intentar obtener un mensaje de error más amigable
    try {
      const { getNetworkErrorMessage } = await import('../utils/networkUtils');
      enhancedError.userMessage = getNetworkErrorMessage(error);
    } catch (e) {
      // Si falla, usar el mensaje original
      enhancedError.userMessage = error.message;
    }
    
    // Usar la utilidad de registro de errores
    logError(enhancedError, 'API_fetchData');
    
    throw enhancedError;
  }
};

// Servicio de preguntas
export const preguntasService = {
  // Obtener todas las preguntas
  getAll: async () => {
    return await fetchData('preguntas.php');
  },

  // Obtener una pregunta por su ID
  getById: async (id) => {
    return await fetchData(`preguntas.php?id=${id}`);
  },

  // Crear una nueva pregunta
  create: async (pregunta) => {
    return await fetchData('preguntas.php', {
      method: 'POST',
      body: JSON.stringify(pregunta)
    });
  },

  // Actualizar una pregunta existente
  update: async (id, pregunta) => {
    // Asegurarse de que el id esté incluido en los datos enviados
    const datosActualizados = {
      id,
      ...pregunta
    };
    
    return await fetchData('preguntas.php', {
      method: 'PUT',
      body: JSON.stringify(datosActualizados)
    });
  },

  // Eliminar una pregunta
  delete: async (id) => {
    return await fetchData('preguntas.php', {
      method: 'DELETE',
      body: JSON.stringify({ id })
    });
  },

  // Aprobar una pregunta
  aprobar: async (id) => {
    return await fetchData('preguntas.php', {
      method: 'PUT',
      body: JSON.stringify({ id, estado: 'aprobado' })
    });
  },

  // Rechazar una pregunta
  rechazar: async (id) => {
    return await fetchData('preguntas.php', {
      method: 'PUT',
      body: JSON.stringify({ id, estado: 'rechazado' })
    });
  }
};

// Servicio de autenticación
export const authService = {
  // Iniciar sesión
  login: async (email, password) => {
    try {
      // Simulación: en futuro vendrá de la API
      const user = usuarios.find(
        (u) => u.email === email && u.password === password
      );
      
      if (user && user.aprobado) {
        return { success: true, user };
      } else if (user && !user.aprobado) {
        throw new Error('Tu cuenta está pendiente de aprobación.');
      } else {
        throw new Error('Email o contraseña incorrectos.');
      }
    } catch (error) {
      logError(error, 'AUTH_login');
      throw error;
    }
  },

  // Registrar un nuevo usuario
  register: async (userData) => {
    try {
      // Simulación: en futuro enviará a la API
      // Verificar si el email ya existe
      const emailExists = usuarios.some(u => u.email === userData.email);
      if (emailExists) {
        throw new Error('Este email ya está registrado.');
      }
      
      // Crear nuevo usuario (simulado)
      const newUser = {
        id: usuarios.length + 1,
        ...userData,
        aprobado: false, // Todos los usuarios nuevos requieren aprobación
        rol: 'usuario'
      };
      
      return { success: true, message: 'Registro exitoso. Tu cuenta está pendiente de aprobación.' };
    } catch (error) {
      logError(error, 'AUTH_register');
      throw error;
    }
  },

  // Verificar estado de la sesión
  checkSession: async () => {
    try {
      // En una implementación real, verificaría con el backend
      // si hay una sesión activa mediante cookies o tokens
      return null; // Por ahora retorna null (no hay sesión)
    } catch (error) {
      logError(error, 'AUTH_checkSession');
      return null;
    }
  },

  // Cerrar sesión
  logout: async () => {
    try {
      // En una implementación real, invalidaría la sesión en el backend
      return { success: true };
    } catch (error) {
      logError(error, 'AUTH_logout');
      throw error;
    }
  }
};

