// Servicio de búsqueda
export const searchService = {
  // Buscar preguntas por texto
  searchPreguntas: async (query, options = {}) => {
    try {
      if (!query || query.trim() === '') {
        return [];
      }
      
      // Opciones por defecto
      const defaultOptions = {
        categoria: null,
        etiqueta: null,
        dificultad: null,
        estado: 'aprobado',
        limit: 20,
        offset: 0
      };
      
      const searchOptions = { ...defaultOptions, ...options };
      
      // Simulación: en futuro haría una búsqueda real en la API
      // Por ahora, simular resultados aleatorios
      const resultados = [];
      const numResultados = Math.floor(Math.random() * 5) + 1; // Entre 1 y 5 resultados
      
      for (let i = 0; i < numResultados; i++) {
        const categoriaIndex = Math.floor(Math.random() * Object.keys(categorias).length);
        const categoria = Object.keys(categorias)[categoriaIndex];
        
        const etiquetaIndex = Math.floor(Math.random() * categorias[categoria].length);
        const etiqueta = categorias[categoria][etiquetaIndex];
        
        const dificultades = ['fácil', 'medio', 'difícil'];
        const dificultadIndex = Math.floor(Math.random() * dificultades.length);
        
        resultados.push({
          id: Math.floor(Math.random() * 1000) + 1,
          pregunta: `Pregunta de ejemplo que contiene "${query}"`,
          categoria,
          etiqueta,
          dificultad: dificultades[dificultadIndex],
          autor: 'Usuario Ejemplo',
          fecha_creacion: new Date().toISOString()
        });
      }
      
      return resultados;
    } catch (error) {
      logError(error, 'SEARCH_searchPreguntas');
      throw error;
    }
  },

  // Buscar usuarios por nombre o email
  searchUsuarios: async (query, options = {}) => {
    try {
      if (!query || query.trim() === '') {
        return [];
      }
      
      // Opciones por defecto
      const defaultOptions = {
        rol: null,
        aprobado: true,
        limit: 20,
        offset: 0
      };
      
      const searchOptions = { ...defaultOptions, ...options };
      
      // Simulación: filtrar usuarios que coincidan con la búsqueda
      const queryLower = query.toLowerCase();
      let resultados = usuarios.filter(usuario => {
        const nombreMatch = usuario.nombre && usuario.nombre.toLowerCase().includes(queryLower);
        const emailMatch = usuario.email && usuario.email.toLowerCase().includes(queryLower);
        
        // Aplicar filtros adicionales si se especifican
        const rolMatch = searchOptions.rol ? usuario.rol === searchOptions.rol : true;
        const aprobadoMatch = searchOptions.aprobado !== null ? usuario.aprobado === searchOptions.aprobado : true;
        
        return (nombreMatch || emailMatch) && rolMatch && aprobadoMatch;
      });
      
      // Aplicar paginación
      resultados = resultados.slice(
        searchOptions.offset,
        searchOptions.offset + searchOptions.limit
      );
      
      // Eliminar información sensible como contraseñas
      return resultados.map(({ password, ...rest }) => rest);
    } catch (error) {
      logError(error, 'SEARCH_searchUsuarios');
      throw error;
    }
  },

  // Búsqueda global (preguntas y usuarios)
  searchGlobal: async (query, options = {}) => {
    try {
      if (!query || query.trim() === '') {
        return { preguntas: [], usuarios: [] };
      }
      
      // Realizar ambas búsquedas en paralelo
      const [preguntas, usuarios] = await Promise.all([
        searchService.searchPreguntas(query, options.preguntas || {}),
        searchService.searchUsuarios(query, options.usuarios || {})
      ]);
      
      return { preguntas, usuarios };
    } catch (error) {
      logError(error, 'SEARCH_searchGlobal');
      throw error;
    }
  }
};

